let options = {
    // new VueHtml5Editor(options) global component name
    name: "vue-html5-editor",
    //  if set true,will append module name to toolbar
    // after icon
    showModuleName: false,
    // classfont-awesome custom icon class of built-in
    // modules,default using font-awesome
    icons: {
        text: "iconfont icon-pencil",
        color: "iconfont icon-beijingse",
        font: "iconfont icon-font",
        align: "iconfont icon-aligncenter",
        list: "iconfont icon-list",
        link: "iconfont icon-chain",
        unlink: "iconfont icon-chain-broken",
        tabulation: "iconfont icon-table",
        image: "iconfont icon-image",
        hr: "iconfont icon-hr",
        eraser: "iconfont icon-eraser",
        undo: "iconfont icon-undo",
        "full-screen": "iconfont icon-arrowsalt",
        info: "iconfont icon-info"
    },
    //  config image module
    image: {
        //   max file size
        sizeLimit: 512 * 1024,
        // ,base64 upload config,default null and convert image to base64
        upload: {
            url: null,
            headers: {},
            params: {},
            fieldName: {}
        },
        compress: {
            width: 1600,
            height: 1600,
            quality: 80
        },
        // , handle response datareturn image url
        uploadHandler(responseText) {
            // default accept json data like  {ok:false,msg:"unexpected"} or
            // {ok:true,data:"image url"}
            var json = JSON.parse(responseText)
            if (!json.ok) {
                alert(json.msg)
            } else {
                return json.data
            }
        }
    },
    // en-uszh-cn default en-us, en-us and zh-cn are built-in
    language: "zh-cn",
    // 
    i18n: {
        //specify your language here
        "zh-cn": {
            "align": "",
            "image": "",
            "list": "",
            "link": "",
            "unlink": "",
            "table": "",
            "font": "",
            "full screen": "",
            "text": "",
            "eraser": "",
            "info": "",
            "color": "",
            "please enter a url": "",
            "create link": "",
            "bold": "",
            "italic": "",
            "underline": "",
            "strike through": "",
            "subscript": "",
            "superscript": "",
            "heading": "",
            "font name": "",
            "font size": "",
            "left justify": "",
            "center justify": "",
            "right justify": "",
            "ordered list": "",
            "unordered list": "",
            "fore color": "",
            "background color": "",
            "row count": "",
            "column count": "",
            "save": "",
            "upload": "",
            "progress": "",
            "unknown": "",
            "please wait": "",
            "error": "",
            "abort": "",
            "reset": ""
        }
    },
    //  the modules you don't want
    hiddenModules: [
        'info', 'image'
    ],
    //  keep only the modules you want and customize the order. can
    // be used with hiddenModules together
    visibleModules: [
        "text",
        "color",
        "font",
        "align",
        "list",
        "link",
        "unlink",
        "tabulation",
        "image",
        "hr",
        "eraser",
        "undo",
        "full-screen",
        "info"
    ],
    // examples extended modules
    modules: {
        //omit,reference to source code of build-in modules
    }
}


export default options;


