<template>
    <div class="wrap">
        <!---->
        <div class="nav-bar clear-fix">
            <el-breadcrumb separator=">" class="fl-l">
                <el-breadcrumb-item></el-breadcrumb-item>
                <el-breadcrumb-item></el-breadcrumb-item>
            </el-breadcrumb>
            <!---->
            <div class="add-menu fl-r" v-if="authOps.includes('/auth/api/menu/save')">
                <el-button type="warning" class="add-menu-btn" @click="addForm_btn('')" size="mini"></el-button>
            </div>
        </div>
    
        <!---->
        <div class="search">
            <el-form :inline="true" :model="menuSearch" class="demo-form-inline">
                
                <el-form-item label="">
                    <el-input v-model="menuSearch.menuName" placeholder="" size="small"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="menuSearch.parentMenuName" placeholder="" size="small"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-select v-model="menuSearch.institutionTypeId" placeholder="" size="small">
                        <el-option label="" value=""></el-option>
                        <el-option :label="val.institutionTypeName" :value="val.institutionTypeId" v-for="(val,key) in instList" :key="key"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item class="search-btn">
                    <el-button type="primary" @click="search_smt"></el-button>
                </el-form-item>
            </el-form>
        </div>
    
        <!---->
        <div class="menu-list">
    
            <!---->
            <el-dialog :title="menuId==''?'':''" :visible.sync="dialogAddForm" class="add-menu-form" :close-on-click-modal="false">
                <el-form :model="addForm" label-width="100px" ref="addForm" :rules="addFormRules">
                    <el-form-item label="" prop="menuId">
                        <el-input v-model="addForm.menuId" placeholder="" :disabled="menuId!=''"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="menuName">
                        <el-input v-model="addForm.menuName" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="Uri" prop="menuUri">
                        <el-input v-model="addForm.menuUri" placeholder="Uri"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="institutionTypeId">
                        <el-select v-model="addForm.institutionTypeId" placeholder="" size="small" @change="instChecked">
                            <el-option :label="val.institutionTypeName" :value="val.institutionTypeId" v-for="(val,key) in instList" :key="key"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="" prop="parentId">
                        <el-select v-model="addForm.parentId" placeholder="" size="small" filterable >
                            <el-option :label="val.menuName" :value="val.menuId" v-for="(val,key) in parentList" :key="key"></el-option>
                        </el-select>
                    </el-form-item>
    
                    <el-form-item label="" prop="menuIco">
                        <el-select v-model="addForm.menuIco" placeholder="" size="small">
                            <el-option :value="item.value" :label="item.label" v-for="item in iconfontList" :key="item.value">
                                <i :class="'iconfont icon-'+item.value"></i>
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="">
                        <el-radio class="radio" v-model="addForm.status" label="1"></el-radio>
                        <el-radio class="radio" v-model="addForm.status" label="0"></el-radio>
                    </el-form-item>
                    <el-form-item label="">
                        <el-radio class="radio" v-model="addForm.isOpen" label="1"></el-radio>
                        <el-radio class="radio" v-model="addForm.isOpen" label="0"></el-radio>
                    </el-form-item>
                    <el-form-item label="" prop="menuSeq">
                        <el-input v-model.number="addForm.menuSeq" placeholder="" type="text"></el-input>
                    </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="dialogAddForm = false"> </el-button>
                    <el-button type="primary" @click="submit_addMenu"> </el-button>
                </div>
            </el-dialog>
    
            <!---->
            <el-table :data="menuData" stripe style="width: 100%" v-loading="menu_list_loading">
                <el-table-column label="" prop="menuId"></el-table-column>
                <el-table-column label="" prop="menuName"></el-table-column>
                <el-table-column label="" align="center" width="90">
                    <template slot-scope="scope">
                        <i :class="'iconfont icon-'+scope.row.menuIco"></i>
                    </template>
                </el-table-column>
                <el-table-column label="Uri" prop="menuUri"></el-table-column>
                <el-table-column label="" width="100" prop="menuLevel"> </el-table-column>
                <el-table-column label="" prop="institutionTypeName"></el-table-column>
                <el-table-column label="" prop="parentMenuName"></el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <i title="" class="primary iconfont icon-jiecunweihu1" @click="doAction(scope.row.menuId)" v-if="authOps.includes('/auth/api/menu/op/list')"></i>
                        <i title="" class="primary iconfont icon-update" @click="doUpdate(scope.row.menuId)" v-if="authOps.includes('/auth/api/menu/update')"></i>
                        <i title="" class="danger iconfont icon-delete" @click="doDelete(scope.row.menuId)" v-if="authOps.includes('/auth/api/menu/delete')"></i>
                    </template>
                </el-table-column>
            </el-table>
    
            <!---->
            <div class="pagination" v-if="Math.ceil(total/myConfig.pageRows)>1">
                <el-pagination @current-change="handleCurrentChange" :current-page="menuSearch.page" :page-size="menuSearch.rows" layout="total, prev, pager, next, jumper" :total="total">
                </el-pagination>
            </div>
        </div>
    
    </div>
</template>

<style lang="less" scoped>
// table
.iconfont {
    font-size: 20px;
}

.wrap {
    padding-right: 40px;
}

.add-menu-form {
    .el-input {
        width: 80%;
        display: inline-block;
    }
}
</style>
<style lang="less">
.add-menu-form {
    .el-dialog--small {
        width: 40%;
    }

    .el-dialog {
        height: 550px;
    }
    .el-dialog__body {
        overflow-x: auto;
        height: 370px;
    }
    .dialog-footer {
        padding-right: 35px;
    }
}
</style>


<script>
import { mapGetters } from 'vuex';
import iconList from '../../../json/iconList.json';
export default {
    data() {
        let checkMenuSeq = (rule, value, callback) => {
            if (!value) {
                return callback(new Error(''));
            }
            setTimeout(() => {
                if (!Number.isInteger(value)) {
                    callback(new Error(''));
                } else {
                    if (value < 1 || value > 125) {
                        callback(new Error('1-125'));
                    } else {
                        callback();
                    }
                }
            }, 300);
        };
        return {
            //iconfont,
            iconfontList: iconList.iconList,
            //loading
            "menu_list_loading": true,

            //
            menuSearch: {
                menuName: '',
                parentMenuName: '',
                institutionTypeId: '',
                //
                page: 1,
                rows: this.myConfig.pageRows
            },
            total: 0,
            //menuID
            menuId: '',

            //
            instList: [],
            instTypeId: '',

            //
            menuData: [],

            //
            parentList: [],

            //
            dialogAddForm: false,
            addForm: {
                institutionTypeId: "",
                isOpen: 1,
                menuIco: "",
                menuId: "",
                menuName: "",
                menuSeq: '',
                menuUri: "",
                parentId: "",
                status: 1
            },

            //
            addFormRules: {
                menuId: [
                    { required: true, message: '' },
                    { min: 1, max: 30, message: ' 1  30 ' }
                ],
                menuName: [
                    { required: true, message: '' },
                    { min: 1, max: 30, message: ' 1  30 ' }
                ],
                institutionTypeId: [
                    { required: true, message: '' },
                ],
                menuSeq: [
                    { validator: checkMenuSeq },
                ]

            },


        }
    },
    computed: {
        ...mapGetters({
            authSelect: 'getAuthSelect',
            authOps: 'getAuthOps'
        })
    },
    methods: {
        //
        search_smt() {
            this.menuSearch.page = 1;
            this.getMenuList();
        },

        //
        addForm_btn(id) {
            this.getInstList();
            this.dialogAddForm = true;
            this.menuId = '';
            this.addForm = {
                institutionTypeId: "",
                isOpen: '1',
                menuIco: "",
                menuId: "",
                menuName: "",
                menuSeq: '',
                menuUri: "",
                parentId: "",
                status: '1'
            }
        },

        //
        getMenuList() {

            this.menu_list_loading = true;//test
            this.axios.post(this.myConfig.code.auth + '/api/menu/list', { body: this.menuSearch }).then(response => {
                let data = response.data;
                this.total = data.body.total;
                if (data.head.code = "10000") {
                    this.menu_list_loading = false;
                    this.menuData = data.body.rows;
                }
            }).catch(error => {
                console.log(error)
            })
        },

        //
        getInstList() {
            let that = this;
            that.axios.post(this.myConfig.code.auth + '/api/inst/list', {}).then(response => {
                let data = response.data;
                if (data.head.code = "10000") {
                    that.instList = data.body.rows;
                }
            }).catch(error => {
                console.log(error)
            })
        },
        instChecked(val) {
            this.instTypeId = val;
            this.getParentList();

        },
        //
        getParentList() {
            this.axios.post(this.myConfig.code.auth + '/api/menu/parent', { body: { instTypeId: this.instTypeId } }).then(response => {
                let data = response.data;
                if (data.head.code = "10000") {
                    this.parentList = data.body.rows;
                }
            })
        },

        //
        getMenuDetail(id) {
            this.axios.post(this.myConfig.code.auth + '/api/menu/detail', { body: { menuId: id } }).then(response => {
                let data = response.data;
                data.body.status = data.body.status.toString();
                data.body.isOpen = data.body.isOpen.toString();
                this.addForm = data.body;
                this.menuId = this.addForm.menuId;
                this.instTypeId = this.addForm.institutionTypeId;
                this.getParentList();
                this.dialogAddForm = true;
            })
        },

        //
        submit_addMenu() {
            this.$refs.addForm.validate((valid) => {
                if (valid) {
                    if (this.menuId == '') {//
                        this.axios.post(this.myConfig.code.auth + '/api/menu/save', { body: this.addForm }).then(response => {
                            let data = response.data;
                            if (data.head.code === '10000') {//
                                this.getMenuList();
                                this.dialogAddForm = false;
                            } else {//
                                this.$message({
                                    type: "error",
                                    message: data.body.sub_msg,
                                    duration: 1000
                                })
                            }
                        }).catch(error => {
                            console.log(error)
                        })
                    } else {//
                        this.axios.post(this.myConfig.code.auth + '/api/menu/update', { body: this.addForm }).then(response => {
                            let data = response.data;
                            if (data.head.code === '10000') {//
                                this.getMenuList();
                                this.dialogAddForm = false;
                            } else {//
                                this.$message({
                                    type: "error",
                                    message: data.body.sub_msg,
                                    duration: 1000
                                })
                            }
                        }).catch(error => {
                            console.log(error)
                        })
                    }
                }
            })
        },

        //
        doUpdate(id) {
            this.getInstList();
            this.getMenuDetail(id);
        },
        //
        doDelete(id) {
            let that = this;
            this.$confirm('?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning',
                closeOnClickModal: false
            }).then(() => {
                that.axios.post(this.myConfig.code.auth + '/api/menu/delete', { body: { menuId: id } }).then(response => {
                    let data = response.data;
                    if (data.head.code === '10000') {//
                        that.getMenuList();
                        that.$message({
                            type: "success",
                            message: '',
                            duration: 1000
                        })
                    } else {//
                        that.$message({
                            type: "error",
                            message: data.body.sub_msg,
                            duration: 1000
                        })
                    }
                }).catch(error => {
                    console.log(error)
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '',
                    duration: 1000
                });
            });
        },

        //
        doAction(id) {
            this.$router.push('/auth/menuMgmt/actMgmt/' + id);
        },
        //
        handleCurrentChange(val) {
            this.menuSearch.page = val;
            this.getMenuList();
        }
    },
    created() {

        //
        this.getMenuList();
        this.getInstList()


    }
}
</script>




