<template>
    <div class="wrap-inst">
        <!---->
        <div class="nav-bar clear-fix">
            <el-breadcrumb separator=">" class="fl-l">
                <el-breadcrumb-item></el-breadcrumb-item>
                <el-breadcrumb-item></el-breadcrumb-item>
            </el-breadcrumb>
            <!---->
            <div class="add-inst fl-r">
                <el-button type="warning" class="add-inst-btn" @click="addForm_btn('')" size="mini">
                    <i class="iconfont icon-add"></i></el-button>
            </div>
        </div>
        <!---->
        <el-dialog :title="addForm.instId==''?'':''" :visible.sync="dialogAddForm" class="add-inst-form" size="tiny" :close-on-click-modal="false">
            <el-form :model="addForm" label-width="100px" ref="addForm" :rules="addFormRules">
                <el-form-item label="" prop="instName">
                    <el-input v-model="addForm.instName" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" prop="contact">
                    <el-input v-model="addForm.contact" placeholder=""></el-input>
                </el-form-item>
    
                <el-form-item label="" prop="phone">
                    <el-input v-model="addForm.phone" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="email" prop="email">
                    <el-input v-model="addForm.email" placeholder="email"></el-input>
                </el-form-item>
                <el-form-item label="" prop="status">
                    <el-select v-model="addForm.status" placeholder="">
                        <el-option label="" value="1"></el-option>
                        <el-option label="" value="0"></el-option>
                    </el-select>
                </el-form-item>
    
                <el-form-item label="" prop="userAccount" v-if="addForm.instId==''">
                    <el-input v-model="addForm.userAccount" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" prop="userPassword" v-if="addForm.instId==''">
                    <el-input v-model="addForm.userPassword" placeholder="" type="password"> </el-input>
                </el-form-item>
                <el-form-item label="" v-if="addForm.instId==''" prop="userPasswordCheck">
                    <el-input v-model="addForm.userPasswordCheck" placeholder="" type="password"></el-input>
                </el-form-item>
    
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogAddForm = false"> </el-button>
                <el-button type="primary" @click="submit_add"> </el-button>
            </div>
        </el-dialog>
        <!--  -->
        <div class="search">
            <el-form :inline="true" :model="searchForm" class="demo-form-inline" label-width="100px">
                <el-form-item label="">
                    <el-input v-model="searchForm.instName" placeholder="" size="small"></el-input>
                </el-form-item>
                <el-form-item class="search-btn">
                    <el-button type="primary" @click="search_smt" size=""></el-button>
                </el-form-item>
            </el-form>
        </div>
    
        <!---->
        <el-table :data="instList" stripe style="width: 100%" v-loading="loading" class="report" border>
            <el-table-column label="ID" prop="instId" width="100"></el-table-column>
            <el-table-column label="" prop="instName"  width="120" show-overflow-tooltip></el-table-column>
            <el-table-column label="" prop="contact" width="150" show-overflow-tooltip> </el-table-column>
            <el-table-column label="" prop="phone" width="150" show-overflow-tooltip> </el-table-column>
            <el-table-column label="email" prop="email" min-width="120" show-overflow-tooltip> </el-table-column>
            <el-table-column label="" width="90">
                <template slot-scope="scope">{{scope.row.status==1?'':''}}</template>
            </el-table-column>
            <el-table-column label="" prop="account" width="120" show-overflow-tooltip> </el-table-column>
            <el-table-column label="" width="165">
                <template slot-scope="scope">{{scope.row.createTime|dateFormat}}</template>
            </el-table-column>
            <el-table-column label="" width="250" fixed="right" align="center">
                <template slot-scope="scope">
                    <el-button size="small" type="primary" @click="doUpdate(scope.row.instId)"></el-button>
                    <el-button size="small" type="danger" @click="doDelete(scope.row.instId)"></el-button>
                </template>
            </el-table-column>
        </el-table>
    
        <!---->
        <div class="pagination" v-if="Math.ceil(total/myConfig.pageRows)>1">
            <el-pagination @current-change="handleCurrentChange" :current-page="searchForm.page" :page-size="searchForm.pageSize" layout="total, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </div>
    </div>
</template>


<style lang="less" scoped>
.wrap-inst {
    padding-right: 40px;
}
</style>

<script>
import { mapGetters } from 'vuex'
import format from 'date-format';
export default {
    data() {
        let checkPwd = (rule, value, callback) => {
            if (!value) {
                return callback(new Error(''));
            }
            setTimeout(() => {
                if (value !== this.addForm.userPassword) {
                    callback(new Error('!'));
                } else {
                    callback();
                }
            }, 500);
        };
        return {
            //
            loading: true,
            //
            searchForm: {
                instName: '',
                page: 1,
                pageSize: this.myConfig.pageRows
            },
            total: 0,
            //
            instList: [],
            //
            addForm: {
                instId: "",
                instName: "",
                contact: "",
                email: "",
                phone: "",
                status: '1',
                userAccount: "",
                userPassword: "",
                userPasswordCheck: ""
            },
            dialogAddForm: false,
            //
            addFormRules: {
                instName: [{ required: true, message: '' }],
                contact: [{ required: true, message: '' }],
                email: [{ required: true, message: 'email' }],
                phone: [{ required: true, message: '' }],
                userAccount: [{ required: true, message: '' }],
                userPassword: [{ required: true, message: '' }],
                userPasswordCheck: [{ validator: checkPwd }]
            },
        }
    },
    computed: {
        ...mapGetters({
            authSelect: 'getAuthSelect',
            authOps: 'getAuthOps'
        })
    },
    filters: {
        dateFormat(date) {
            return format('yyyy-MM-dd hh:mm', new Date(date));
        }
    },
    methods: {
        //
        search_smt() {
            this.searchForm.page = 1;
            //
            for (let i in this.searchForm) {
                if (typeof this.searchForm[i] == 'string') {
                    this.searchForm[i] = this.searchForm[i].trim();
                }
            }
            this.getInstList();
        },

        //
        getInstList() {
            this.loading = true;
            this.axios.post(this.myConfig.code.proxy + '/translate/api/inst/query', { body: this.searchForm }).then(response => {
                let data = response.data;
                if (data.head.code == '10000') {
                    this.loading = false;
                    this.instList = data.body.rows;
                    this.total = data.body.total;
                }
            })
        },

        //
        getInstDetail(id) {
            this.axios.post(this.myConfig.code.proxy + '/translate/api/inst/query', { body: { instId: id } }).then(response => {
                let data = response.data;
                if (data.head.code == '10000') {
                    this.addForm = data.body.rows[0];
                    this.addForm.status = this.addForm.status.toString();
                    this.dialogAddForm = true;
                }
            })

        },

        // 
        addForm_btn() {
            this.dialogAddForm = true;
            this.addForm = {
                instId: "",
                instName: "",
                contact: "",
                email: "",
                phone: "",
                status: '1',
                userAccount: "",
                userPassword: "",
                userPasswordCheck: ""
            };
        },
        // 
        submit_add() {
            this.$refs.addForm.validate((validate) => {
                this.$refs.addForm.validate((validate) => {
                    this.axios.post(this.myConfig.code.proxy + '/translate/api/inst/save', { body: this.addForm }).then(response => {
                        let data = response.data;
                        if (data.head.code === '10000') {//
                            this.getInstList();
                            this.dialogAddForm = false;
                        }
                    })
                })
            })

        },
        //
        doUpdate(id) {
            this.addForm.instId = id
            this.getInstDetail(id);
        },
        //
        doDelete(id) {
            this.$confirm('?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                this.axios.post(this.myConfig.code.proxy + '/translate/api/inst/delete', { body: { instId: id } }).then(response => {
                    let data = response.data;
                    if (data.head.code === '10000') {//
                        this.getInstList();
                        this.$message({
                            type: "success",
                            message: '',
                            duration: 1000
                        })
                    }
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '',
                    duration: 1000
                });
            });
        },
        //
        handleCurrentChange(val) {
            this.searchForm.page = val;
            this.getInstList();
        }

    },
    mounted() {
        this.getInstList();
    }
}
</script>




