<template>
    <div class="wrap">
        <!---->
        <div class="nav-bar clear-fix">
            <el-breadcrumb separator=">" class="fl-l">
                <el-breadcrumb-item></el-breadcrumb-item>
                <el-breadcrumb-item></el-breadcrumb-item>
            </el-breadcrumb>

            <!---->
            <div class="add-menu fl-r" v-if="authOps.includes('/auth/api/user/save')">
                <el-button type="warning" class="add-menu-btn" @click="addForm_btn('')" size="mini">
                    <i class="iconfont icon-add"></i></el-button>
            </div>
        </div>

        <div class="search">
            <el-form :inline="true" :model="searchForm" class="demo-form-inline">
                <i class="el-icon-search"></i>
                <el-form-item label="">
                    <el-input v-model="searchForm.userName" placeholder="" size="small"></el-input>
                </el-form-item>
                <el-form-item class="search-btn">
                    <el-button type="primary" @click="search_smt"></el-button>
                </el-form-item>
            </el-form>
        </div>

        <div class="menu-list">

            <!---->
            <el-dialog :title="userId!=''?'':''" :visible.sync="dialogAddForm" size="tiny" :close-on-click-modal="false">
                <el-form :model="addForm" label-width="100px" ref="addForm" :rules="addFormRules">
                    <el-form-item label="" prop="userAccount" :rules="{required:true,message:''}">
                        <el-input v-model="addForm.userAccount" placeholder="" :disabled="userId!=''"></el-input>
                    </el-form-item>
                    <el-form-item label="" v-if="userId==''" prop="userPassword" :rules="{required:true,message:''}">
                        <el-input v-model="addForm.userPassword" placeholder="" type="password"></el-input>
                    </el-form-item>
                    <el-form-item label="" v-if="userId==''" prop="userPasswordCheck" class="userPasswordCheck">
                        <el-input v-model="addForm.userPasswordCheck" placeholder="" type="password"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="userName" :rules="{required:true,message:''}">
                        <el-input v-model="addForm.userName" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="roleId" :rules="{required:true,message:''}">
                        <el-select v-model="addForm.roleId" placeholder="">
                            <el-option :label="val.roleName" :value="val.roleId" v-for="(val,key) in roleList" :key="key"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="" prop="phone" :rules="{required:true,message:''}">
                        <el-input v-model="addForm.phone" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="email" prop="email" :rules="[{ required: true, message: '' },{ type: 'email', message: '' }]">
                        <el-input v-model="addForm.email" placeholder="email"></el-input>
                    </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="dialogAddForm = false"> </el-button>
                    <el-button type="primary" @click="submit_adduser"> </el-button>
                </div>
            </el-dialog>

            <!---->
            <el-table :data="userList" stripe style="width: 100%" v-loading="loading" border>
                <el-table-column prop="userId" label="ID" align="center"></el-table-column>
                <el-table-column prop="userAccount" label="" align="center"></el-table-column>
                <el-table-column prop="userName" label="" align="center"></el-table-column>
                <el-table-column prop="roleName" label="" align="center"></el-table-column>
                <el-table-column prop="lastLoginTime" label="" align="center"></el-table-column>
                <el-table-column label="" width="250">
                    <template slot-scope="scope">

                        <i title="" class="primary iconfont icon-chakan" @click="doAction(scope.row.userId)" v-if="authOps.includes('/auth/api/user/detail')"></i>
                        <i title="" class="primary iconfont icon-update" @click="doUpdate(scope.row.userId)" v-if="authOps.includes('/auth/api/user/update')"></i>
                        <i title="" class="primary iconfont icon-reset" @click="doReset(scope.row.userId)" v-if="authOps.includes('/auth/api/user/update')"></i>
                        <i title="" class="danger iconfont icon-delete" @click="doDelete(scope.row.userId)" v-if="authOps.includes('/auth/api/user/delete') && scope.row.isAdmin==0 "></i>
                    </template>
                </el-table-column>
            </el-table>

            <!---->
            <div class="pagination" v-if="Math.ceil(total/myConfig.pageRows)>1">
                <el-pagination @current-change="handleCurrentChange" :current-page="searchForm.page" :page-size="searchForm.rows" layout="total, prev, pager, next, jumper" :total="total">
                </el-pagination>
            </div>

            <!---->
            <el-dialog title="" :visible.sync="dialogUserDetail" class="dialogUserDetail" size="tiny" :close-on-click-modal="false">
                <div class="list-box ">
                    <div class="list ">
                        <span class="label"></span>
                        <span class="txt">{{addForm.userId}}</span>
                    </div>
                    <div class="list ">
                        <span class="label"></span>
                        <span class="txt">{{addForm.userAccount}}</span>
                    </div>
                    <div class="list ">
                        <span class="label"></span>
                        <span class="txt">{{addForm.userName}}</span>
                    </div>
                    <div class="list ">
                        <span class="label"></span>
                        <span class="txt">{{addForm.roleId}}</span>
                    </div>
                    <div class="list ">
                        <span class="label"></span>
                        <span class="txt">{{addForm.phone}}</span>
                    </div>
                    <div class="list ">
                        <span class="label">email</span>
                        <span class="txt">{{addForm.email}}</span>
                    </div>
                </div>
            </el-dialog>

        </div>

    </div>
</template>

<style lang="less" scoped>
.wrap {
    padding-right: 40px;
}
</style>

<style lang="less">
.dialogUserDetail {
    .list {
        height: 36px;
        line-height: 36px;
        .label {
            display: inline-block;
            width: 100px;
            text-align: right;
            margin-right: 60px;
            color: #A5C5EF;
        }
        .txt {
            color: #222;
        }
    }
}

.userPasswordCheck {
    label {
        &::before {
            content: '*';
            color: #ff4949;
            margin-right: 4px;
        }
    }
}
</style>


<script>
import { mapGetters } from 'vuex'
export default {
    data() {
        let checkPwd = (rule, value, callback) => {
            if (!value) {
                return callback(new Error(''));
            }
            setTimeout(() => {
                if (value !== this.addForm.userPassword) {
                    callback(new Error('!'));
                } else {
                    callback();
                }
            }, 500);
        };
        return {
            //
            loading: true,
            //
            searchForm: {
                userName: '',
                page: 1,
                rows: this.myConfig.pageRows
            },

            //
            userList: [],

            //id
            userId: [],

            //
            roleList: [],
            total: 0,
            //
            addForm: {
                email: "",
                phone: "",
                roleId: "",
                userAccount: "",
                userName: "",
                userPassword: "",
                userPasswordCheck: ""
            },

            //
            addFormRules: {
                userPasswordCheck: [{ validator: checkPwd }]

            },
            dialogAddForm: false,
            dialogUserDetail: false,
            userId: ""
        }
    },
    computed: {
        ...mapGetters({
            authSelect: 'getAuthSelect',
            authOps: 'getAuthOps'
        })
    },

    methods: {

        //
        search_smt() {
            this.searchForm.page = 1;
            this.getuserList();
        },

        //
        getuserList() {
            this.loading = true;
            this.axios.post(this.myConfig.code.auth + '/api/user/list', { body: this.searchForm }).then(response => {
                let data = response.data;
                if (data.head.code == '10000') {
                    this.loading = false;
                    this.userList = data.body.rows;
                    this.total = data.body.total;
                }
            }).catch(error => {
                console.log(error)
            })
        },

        //
        getuserDetail(id, param) {

            this.axios.post(this.myConfig.code.auth + '/api/user/detail', { body: { userId: id } }).then(response => {
                let data = response.data;
                if (data.head.code == '10000') {
                    this.addForm = data.body;
                    if (param == 'detail') {
                        this.dialogUserDetail = true;
                    } else {
                        this.dialogAddForm = true;
                    }
                }
            })

        },

        //
        getRoleList() {
            this.axios.post(this.myConfig.code.auth + '/api/role/combobox', {}).then(response => {
                let data = response.data;
                if (data.head.code = "10000") {
                    this.roleList = data.body.rows;
                }
            }).catch(error => {
                console.log(error)
            })
        },
        //
        addForm_btn() {
            this.userId = '';
            this.getRoleList();
            this.dialogAddForm = true;
            this.addForm = {
                email: "",
                phone: "",
                roleId: "",
                userAccount: "",
                userName: "",
                userPassword: "",
                userPasswordCheck: ""
            };
        },

        //
        submit_adduser() {
            this.$refs.addForm.validate((validate) => {
                if (validate) {
                    if (this.userId == '') {//
                        this.axios.post(this.myConfig.code.auth + '/api/user/save', { body: this.addForm }).then(response => {
                            let data = response.data;
                            if (data.head.code === '10000') {//
                                this.getuserList();
                                this.dialogAddForm = false;
                            }
                        }).catch(error => {
                            console.log(error)
                        })
                    } else {//
                        this.axios.post(this.myConfig.code.auth + '/api/user/update', { body: this.addForm }).then(response => {
                            let data = response.data;
                            if (data.head.code === '10000') {//
                                this.getuserList();
                                this.dialogAddForm = false;
                                this.$router.go(0);
                            }
                        }).catch(error => {
                            console.log(error)
                        })
                    }
                }
            })

        },

        //
        doUpdate(id) {
            this.userId = id;
            this.getRoleList();
            this.getuserDetail(id);
        },
        //
        doReset(id) {
            this.$confirm('?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                this.axios.post(this.myConfig.code.proxy + '/auth/api/user/resetPwd', { body: { userId: id } }).then(response => {
                    let data = response.data;
                    if (data.head.code === '10000') {//
                        this.getuserList();
                        this.$message({
                            type: "success",
                            message: '',
                            duration: 1000
                        })
                    } else {//
                        this.$message({
                            type: "error",
                            message: data.body.sub_msg,
                            duration: 1000
                        })
                    }
                })
            })
        },
        //
        doDelete(id) {
            this.$confirm('?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                this.axios.post(this.myConfig.code.auth + '/api/user/delete', { body: { userId: id } }).then(response => {
                    let data = response.data;
                    if (data.head.code === '10000') {//
                        this.getuserList();
                        this.$message({
                            type: "success",
                            message: '',
                            duration: 1000
                        })
                    } else {//
                        this.$message({
                            type: "error",
                            message: data.body.sub_msg,
                            duration: 1000
                        })
                    }
                }).catch(error => {
                    console.log(error)
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '',
                    duration: 1000
                });
            });
        },
        //
        handleCurrentChange(val) {
            this.searchForm.page = val;
            this.getuserList();
        },
        //
        doAction(id) {
            this.getuserDetail(id, 'detail');
        }

    },
    created() {
        this.getuserList();
    }
}
</script>




