<template>
    <div class="wrap">
        <!---->
        <div class="nav-bar clear-fix">
            <el-breadcrumb separator=">" class="fl-l">
                <el-breadcrumb-item></el-breadcrumb-item>
                <el-breadcrumb-item></el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <!---->
        <div class="search">
            <el-form :inline="true" :model="searchForm" class="demo-form-inline" label-width="100px">
                <el-form-item label="">
                    <el-input v-model="searchForm.auditUser" placeholder="" size="small"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-date-picker type="date" placeholder="" v-model="searchForm.auditStartDate" :picker-options="pickerOptions"></el-date-picker>
                </el-form-item>
                <el-form-item label="">
                    <el-date-picker type="date" placeholder="" v-model="searchForm.auditEndDate" :picker-options="pickerOptions"></el-date-picker>
                </el-form-item>
                <el-form-item class="search-btn">
                    <el-button type="primary" @click="search_smt"></el-button>
                </el-form-item>
            </el-form>
        </div>
        <!---->
        <el-table :data="reportList" stripe style="width: 100%" v-loading="loading" border>
             <el-table-column type="index" width="50" ></el-table-column>
            <el-table-column label="" prop="auditUser" align="center"></el-table-column>
            <el-table-column label="" prop="autoCount" align="center"> </el-table-column>
            <el-table-column label="" prop="supplierCount" align="center"> </el-table-column>
            <el-table-column label="" prop="otherCount" align="center"> </el-table-column>
            <!-- <el-table-column label="">
                <template slot-scope="scope">
                    <el-button size="small" type="primary" @click="doAction(scope.row.auditUser)"></el-button>
                </template>
            </el-table-column> -->
        </el-table>
    
        <!--  -->
        <!-- <el-dialog :title="detailSearch.auditUser+''" :visible.sync="dialogDetailVisible" :close-on-click-modal="false" size="large">
            <el-table :data="detailData" stripe style="width: 100%" v-loading="loading2" height="400">
                <el-table-column property="goodsSn" label="" width="180"></el-table-column>
                <el-table-column property="goodsTitle" label=""></el-table-column>
                <el-table-column property="totalChar" label="" width="140"></el-table-column>
                <el-table-column property="acceptTime" label="" width="180"></el-table-column>
                <el-table-column property="auditTime" label="" width="180"></el-table-column>
            </el-table> -->
            <!---->
            <!-- <div class="pagination" v-if="Math.ceil(total/myConfig.pageRows)>1">
                <el-pagination @current-change="handleCurrentChange" :current-page="detailSearch.page" :page-size="detailSearch.pageSize" layout="total, prev, pager, next, jumper" :total="total">
                </el-pagination>
            </div>
        </el-dialog> -->
    
    </div>
</template>
<style lang="less" scoped>
.wrap {
    padding-right: 40px;
}
</style>

<script>
import format from 'date-format';
export default {
    data() {
        return {
            searchForm: {
                auditUser: '',
                auditStartDate: '',
                auditEndDate: ''
            },
            pickerOptions: {
                disabledDate(time) {
                    if (time.getTime() > Date.now()) {
                        return time
                    } else {
                        return false;
                    }

                }
            },
            //

            loading: false,
            total: 0,
            reportList: [],
            // 
            dialogDetailVisible: false,
            loading2: false,
            detailData: [],
            detailSearch: {
                auditUser: '',
                auditStartDate: '',
                auditEndDate: '',
                taskStatus: 4,
                page: 1,
                pageSize: this.myConfig.pageRows
            }
        }
    },
    methods: {
        search_smt() {
            //
            for (let i in this.searchForm) {
                if (typeof this.searchForm[i] == 'string') {
                    this.searchForm[i] = this.searchForm[i].trim();
                }
            }
            this.getReportList();
        },
        //
        getReportList() {
            this.loading = true;
            if (this.searchForm.auditStartDate != '') {
                this.searchForm.auditStartDate = format('yyyy-MM-dd', new Date(this.searchForm.auditStartDate));
            }
            if (this.searchForm.auditEndDate != '') {
                this.searchForm.auditEndDate = format('yyyy-MM-dd', new Date(this.searchForm.auditEndDate));
            }
            this.axios.post(this.myConfig.code.proxy + '/translate/api/report/auditCount', { body: this.searchForm }).then(response => {
                let data = response.data;
                if (data.head.code == '10000') {
                    this.loading = false;
                    this.reportList = data.body;
                }
            })
        },
        //
        getDetailList() {
            this.loading2 = true;
            if (this.searchForm.auditStartDate != '') {
                this.detailSearch.auditStartDate = format('yyyy-MM-dd', new Date(this.searchForm.auditStartDate));
            } else {
                this.detailSearch.auditStartDate = '';
            }
            if (this.searchForm.auditEndDate != '') {
                this.detailSearch.auditEndDate = format('yyyy-MM-dd', new Date(this.searchForm.auditEndDate));
            } else {
                this.detailSearch.auditEndDate = '';
            }
            this.axios.post(this.myConfig.code.proxy + '/translate/api/task/admin/query', { body: this.detailSearch }).then(response => {
                let data = response.data;
                if (data.head.code == '10000') {
                    this.total = data.body.total;
                    this.loading2 = false;
                    this.detailData = data.body.rows;

                }
            })
        },
        //
        handleCurrentChange(val) {
            this.detailSearch.page = val;
            this.getDetailList();
        },
        doAction(data) {
            this.detailSearch.auditUser = data;
            this.detailSearch.page = 1;
            this.dialogDetailVisible = true;
            this.getDetailList();
        }

    },
    created() {
        this.getReportList();
    }
}
</script>




