//
// components 

import SysMain from '@/components/sys/main.vue'
import Iframe from '@/components/sys/Iframe.vue';
//
import AuthMenuMgmt from '@/components/sys/auth/MenuMgmt.vue';
import MenuActMgmt from '@/components/sys/auth/MenuActMgmt.vue';
//
// const AuthMenuMgmt = r => require.ensure([], () => r(require('@/components/sys/auth/MenuMgmt.vue')), 'chunkname1')
// const MenuActMgmt = r => require.ensure([], () => r(require('@/components/sys/auth/MenuActMgmt.vue')), 'chunkname1')
//
import AuthRoleMgmt from '@/components/sys/auth/RoleMgmt.vue';
//
import AuthUserMgmt from '@/components/sys/auth/UserMgmt.vue';
import MainWelcome from '@/components/sys/Welcome.vue'

//main auth router router 



//auth 
let router_auth_index = {
  path: 'index',
  name: 'auth-index',
  component:MainWelcome
}
let router_auth_menuMgmt = {
  path: "menuMgmt",
  name: "menuMgmt",
  component: AuthMenuMgmt
}
//
let router_auth_menuMgmt_actMgmt = {
  path: "menuMgmt/actMgmt/:id",
  name: "MenuActMgmt",
  component: MenuActMgmt
}

//
let router_auth_roleMgmt = {
  path: "roleMgmt",
  name: "roleMgmt",
  component: AuthRoleMgmt
}

//
let router_auth_userMgmt = {
  path: "userMgmt",
  name: "userMgmt",
  component: AuthUserMgmt
}

let router_autn_iframe = {
    path: 'iframe',
    name: 'auth-iframe',
    component:Iframe
}

// auth main router
let router_auth = {
  path: 'auth',
  name: 'auth',
  redirect: '/auth/index',
  component: SysMain,
  children: [router_auth_index,router_auth_menuMgmt, router_auth_menuMgmt_actMgmt, router_auth_roleMgmt, router_auth_userMgmt,router_autn_iframe]
}

export default router_auth


