<template>
    <div class="wrap-action wrap">
        <!---->
        <div class="nav-bar clear-fix">
            <el-breadcrumb separator=">" class="fl-l">
                <el-breadcrumb-item></el-breadcrumb-item>
                <el-breadcrumb-item :to="{ path: '/sys/menuMgmt' }"></el-breadcrumb-item>
                <el-breadcrumb-item></el-breadcrumb-item>
            </el-breadcrumb>
            <div class="add-menu fl-r" >
            <el-button type="warning" class="add-menu-btn" @click="showAddForm=true" size="mini">
                    </el-button>
            </div>
        </div>
        <div class="action-list">
    
            <!---->
            <el-dialog title='"CPGL"' :visible.sync="showAddForm" class="add-action-form" :close-on-click-modal="false" :before-close="showAddFormClose">
                <div class="add-btn">
                    <span class="tip">10</span>
                    <i class="iconfont icon-add" @click="addList"></i>
                </div>
                <el-form :model="addForm" label-width="100px" :inline="true" class="addForm-box" ref="addForm">
                    <div class="action-item-list clear-fix" v-for="(val,index) in addForm.ops " :key="val.key">
                        <el-form-item class="list-item" label="" :prop="'ops.' + index + '.operationName'" :rules="{required: true, message: ''}">
                            <el-input v-model="val.operationName" auto-complete="off" size="small"></el-input>
                        </el-form-item>
                        <el-form-item label="Uri" :prop="'ops.' + index + '.operationUri'" :rules="{required: true, message: 'Uri'}">
                            <el-input v-model="val.operationUri" auto-complete="off" size="small"></el-input>
                        </el-form-item>
                        <el-button class="list-item" type="danger" size="small" @click="doDeleteActionList(val)" v-if="authOps.includes('/auth/api/menu/op/delete')"></el-button>
                    </div>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="showAddForm = false"> </el-button>
                    <el-button type="primary" @click="submit_addForm"> </el-button>
                </div>
            </el-dialog>
    
            <!---->
            <el-table :data="actionList" stripe style="width: 100%" v-loading="loading">
                <el-table-column prop="operationId" label="" width="180">
                </el-table-column>
                <el-table-column prop="menuId" label="" width="180">
                </el-table-column>
                <el-table-column prop="operationName" label="">
                </el-table-column>
                <el-table-column prop="operationUri" label="Uri">
                </el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <i title="" class="danger iconfont icon-delete" @click="doDeleteAction(scope.row.operationId)"></i>
                    </template>
                </el-table-column>
            </el-table>
    
            <!---->
            <div class="pagination" v-if="Math.ceil(total/myConfig.pageRows)>1">
                <el-pagination @current-change="handleCurrentChange" :current-page="actionSearch.page" :page-size="actionSearch.rows" layout="total, prev, pager, next, jumper" :total="total">
                </el-pagination>
            </div>
        </div>
    </div>
</template>


<style lang="less" scoped>
.wrap {
    padding-right: 40px;
}

.add-menu {
    height: 48px;
    text-align: right;
}

.add-btn {
    padding-bottom: 15px;
    padding-right: 15px;
    text-align: right;
    height: 54px;
    line-height: 54px;
    span,
    i {
        display: inline-block;
        vertical-align: middle;
    }
    i {
        font-size: 36px;
        cursor: pointer;
    }
}
</style>
<style lang="less">
.add-action-form {
    .action-item-list {
        .el-form-item {
            float: left;
            &.button {
                margin-left: 10px;
                margin-top: 4px;
            }
        }
    }
    .el-dialog--small {
        padding: 10px 20px;
        width: 60%;
        .addForm-box {
            max-height: 300px;
            overflow-x: auto;
        }
    }
}
</style>



<script>
import { mapGetters } from 'vuex'
export default {
    data() {
        return {

            //loading...
            loading: true,

            //id
            menuId: '',

            //
            actionList: [],
            form: {
                name: '',
                region: '',
                date1: '',
                date2: '',
                delivery: false,
                type: [],
                resource: '',
                desc: ''
            },
            addForm: {
                ops: [
                    {
                        "operationName": "",
                        "operationUri": ""
                    }
                ]
            },
            showAddForm: false,
            total: 0,
        }
    },
    computed: {
        ...mapGetters({
            authSelect: 'getAuthSelect',
            authOps: 'getAuthOps'
        }),
        //
        actionSearch() {
            return {
                menuId: this.menuId,
                page: 1,
                rows: this.myConfig.pageRows
            }
        }

    },
    methods: {
        ///menu/op/list
        getActionList() {
            let that = this;
            this.loading = true;
            that.axios.post(this.myConfig.code.auth + '/api/menu/op/list', { body: this.actionSearch }).then(response => {
                let data = response.data;
                if (data.head.code == '10000') {
                    this.loading = false;
                    this.actionList = data.body.rows;
                    this.total = data.body.total;
                }
            }).catch(error => {
                console.log(error)
            })
        },
        // input
        addList() {
            if (this.addForm.ops.length > 9) {
                this.$message({
                    type: "error",
                    message: '10',
                    duration: 1000
                })
            } else {
                this.addForm.ops.push({
                    "operationName": "",
                    "operationUri": ""
                })
            }

        },
        //  input
        doDeleteActionList(item) {
            var index = this.addForm.ops.indexOf(item)
            if (index !== -1) {
                this.addForm.ops.splice(index, 1)
            }
        },
        //
        showAddFormClose(done) {
            done();
            this.addForm = {
                ops: [
                    {
                        "operationName": "",
                        "operationUri": ""
                    }
                ]
            }

        },
        // 
        submit_addForm() {
            //
            this.$refs.addForm.validate((validate) => {
                if (validate) {
                    this.axios.post(this.myConfig.code.auth + '/api/menu/op/save', {
                        body: {
                            menuId: this.menuId,
                            ops: this.addForm.ops
                        }
                    }).then(response => {
                        let data = response.data;
                        if (data.head.code == '10000') {
                            this.showAddForm = false;
                            this.getActionList();
                        }

                    }).catch(error => {
                        console.log(error)
                    })
                }
            })
        },
        //
        doDeleteAction(id) {

            console.log(id)
            let that = this;
            this.$confirm('?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                that.axios.post(this.myConfig.code.auth + '/api/menu/op/delete', { body: { operationId: id } }).then(response => {
                    let data = response.data;
                    if (data.head.code === '10000') {//
                        that.getActionList();
                        that.$message({
                            type: "success",
                            message: '',
                            duration: 1000
                        })
                    } else {//
                        that.$message({
                            type: "error",
                            message: data.body.sub_msg,
                            duration: 1000
                        })
                    }
                }).catch(error => {
                    console.log(error)
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '',
                    duration: 1000
                });
            });
        },
        //
        handleCurrentChange(val) {
            this.actionSearch.page = val;
            this.getActionList();
        }
    },
    created() {
        let that = this;
        this.menuId = this.$route.params.id;
        this.getActionList();
    }
}
</script>



